<template>
	<div class="app-page" >
		<div class="container">

			<ul class="nav">
				
				<li class="nav-item">
					<a :class="'nav-link' + (current_tab == 'local'?' disabled':'')" href="#" @click.prevent="current_tab = 'local'">File locali</a>
				</li>
				<li class="nav-item">
					<a :class="'nav-link' + (current_tab == 'remote'?' disabled':'')" href="#" @click.prevent="current_tab = 'remote'">Scarica</a>
				</li>
				
			</ul>

			<hr>

			<div class="row">

				<div class="col-sm-8">
					<!-- FILE REMOTI -->
					<div class="remote_files" v-if="current_tab == 'remote'">
						<h4>File remoti</h4>
						<div class="row mb-2">
							<div class="col-sm-8">
								<input type="text" class="form-control" v-model="media_url">
							</div>
							<div class="col-sm-4">
								<button class="btn btn-primary" @click="getRemoteList">Carica elenco media</button>

							</div>
						</div>


						<table class="table table-sm table-hover">
							<tbody>
								<tr v-for="file in remote_files">
									<td><strong>{{ file.filename }}</strong></td>
									<td><span class="badge">{{ file.size }} bytes</span></td>
									<td><small>{{ file.url }}</small></td>
									<td><span class="text-success" v-if="existsLocal(file.filename)">In locale</span></td>
									<td>
										<button class="btn btn-primary btn-sm" @click="downloadFile(file)" v-if="!file.downloaded" :disabled="file.is_downloading">
										<i class="fa fa-spin fa-circle-notch" v-if="file.is_downloading" ></i>
										Scarica
										</button>
										<button class="btn btn-success btn-sm"  disabled v-else>Scaricato</button>
									</td>
								</tr>
							</tbody>
						</table>
						<hr>
						<div class="text-right">Files disponibili: <strong>{{ remote_files.length }}</strong> - <a href="#" @click.prevent="downloadAll">Scarica tutti</a></div>
					</div>
					<!-- FILE REMOTI FINE -->


					<!-- FILE LOCALI -->
					<div class="remote_files" v-if="current_tab == 'local'">
						<h4>File locali</h4>


						<table class="table table-sm table-hover">
							<tbody>
								<tr v-for="file in local_files">
									<td><strong>{{ file.name }}</strong></td>

									<td><small>{{ file.fullPath }}</small></td>
									<td><button class="btn btn-danger btn-sm" @click="deleteFile(file.name)">Cancella</button></td>
								</tr>
							</tbody>
						</table>

						<hr>
						<div class="text-right">Files trovati: <strong>{{ local_files.length }}</strong> - <a href="#" @click.prevent="loadLocalFiles">Aggiorna</a></div>
					</div>
					<!-- FILE LOCALI FINE -->

				</div>
				<div class="col-sm-4">

						<!-- LOG -->

						<div class="card card-log">
							<div class="card-header">
								<strong>Log</strong>
							</div>
							<div class="card-body">
								
								<div v-for="m in log" :class="'alert alert-sm alert-' + m.type">
									<span>{{ m.date}}</span>
									<strong>{{ m.message}}</strong>
								</div>


							</div>
						
						
						
						<!-- LOG FINE -->
						</div>
				</div>

			</div>
			



		</div>
	</div>
</template>

<script>

	// http://forza4.bl8m.it/server/file_index.php

	import { mapState } from 'vuex';
	import { mapGetters } from 'vuex';

	import axios from 'axios'
	import moment from 'moment'
	import 'moment/locale/it'

	const DOWNLOAD_FOLDER = 'connect4';

	export default {
		name: 'PageFiles',
		mounted: function(){


			var self = this;

			moment.locale('it');
			console.log('FILESYSTEM');
			console.log(cordova.file);
			this.local_dir = cordova.file.dataDirectory + DOWNLOAD_FOLDER;
			console.log(this.local_dir);

			window.addEventListener('filePluginIsReady', function(){ 
				console.log('File plugin is ready');

			}, false);

			// CREO LA CARTELLA
			window.resolveLocalFileSystemURL(cordova.file.dataDirectory, function (fileSystem) {
			    console.log('Creating folder ' + DOWNLOAD_FOLDER);
			    fileSystem.getDirectory(DOWNLOAD_FOLDER, { create: true }, function (file) {
			          console.log("File: "+ file.name + ', ' + file.fullPath);
			   });
			});
			

			// FILE LOCALI
			// carico l'elenco dei file locali
			this.loadLocalFiles();
			

			// DOWNLOADER
			console.log('Downloader Init: '+ this.local_dir);
			// downloader.init({folder: 'Connect4', fileSystem: cordova.file.dataDirectory});
			downloader.init({folder: this.local_dir, fileSystem: cordova.file.dataDirectory});

			document.addEventListener('DOWNLOADER_downloadSuccess', function(event){
				
				var files = event.data;
				var filenames = '';
				for(var i in files){
						var filename = files[i].name;
						console.log('cerco' + filename);

						filenames += filename;
						var downloaded = _.find(self.remote_files, {filename: filename});

						if(downloaded != undefined){
								downloaded.downloaded = true;
								downloaded.is_downloading = false;
						}
						else{
							console.log(filename + ' non trovato');
						}
				}

				self.loadLocalFiles();
				self.notify('File scaricati correttamente: ' + filenames ,'success');
			});

			

			document.addEventListener('DOWNLOADER_downloadSuccess', function(event){

				var data = event.data;
				
			});

			document.addEventListener('DOWNLOADER_getFileError', function(event){
				console.log('DOWNLOADER_getFileError');
				self.notify(event.data, 'danger')
			});



			document.addEventListener('DOWNLOADER_downloadError', function(event){
				console.log('DOWNLOADER_downloadError');
				self.notify(event.data, 'danger')
			});

			document.addEventListener('DOWNLOADER_downloadError', function(event){
				console.log('DOWNLOADER_downloadError');
				self.notify(event.data, 'danger')
			});

		},
		data () {
			return {
				current_tab : 'local',


				media_url: 'http://forza4.bl8m.it/server/file_index.php',

				local_files: [],
				remote_files: [],

				log: [],
			}
		},
		computed: {

			...mapState({

			})
		},
		methods: {

			notify: function(message, type = 'info'){
					var now = moment().format('hh:mm:ss');

					var r = {
						date: now,
						message: message,
						type: type,
					};

					
					this.log.unshift(r);
			},

			loadLocalFiles: function(){

				var self = this;

				self.local_files = [];

				console.log('loadLocalFiles from ' + self.local_dir);

				window.resolveLocalFileSystemURL(self.local_dir,
					function (fileSystem) {
						console.log('File locali');
						var reader = fileSystem.createReader();
						reader.readEntries(
							function (entries) {
								var counter = 0;
								console.log(entries);
								for(var i in entries){
									var entry = entries[i];

									if(entry.isFile && entry.name != 'nomedia'){
										self.local_files.push(entry);
										counter++;
									}
								}


								//self.notify('File locali trovati: ' + counter);
							},
							function (err) {

								self.notify(err, 'danger');
							}
							);
					}, function (err) {

						self.notify(err, 'warning');
					}
					);
			},


			getRemoteList: function(){


				axios.get( this.media_url , {
					crossdomain: true
				})
				.then(response => {
					this.remote_files = response.data;
				})
				.catch(error => {
					console.log(error);
				})


				
			},

			existsLocal: function(filename){
					var local_file = _.find(this.local_files, {name: filename});

					return (local_file == undefined ? false : true);
			},


			downloadFile: function(file){

				console.log('Downloading from ' + file.url);
				file.is_downloading = true;

				downloader.get(file.url);
				

			},


			downloadAll: function(){

				console.log('Downloading all files ');

				var files = this.remote_files.map( function(item){
						return {
							url: item.url
						};
				});

				downloader.getMultipleFiles(files);

			},

			deleteFile: function(filename){
				var self = this;

				if(confirm('Procedere con la cancellazione di ' + filename + '?')){

						window.resolveLocalFileSystemURL(this.local_dir, function(dir) {
							dir.getFile(filename, {create:false}, function(fileEntry) {
													fileEntry.remove(function(){
															// The file has been removed succesfully
															self.notify('File cancellato correttamente: ' + filename, 'success');
															self.loadLocalFiles();
													},function(error){
															// Error deleting the file
															self.notify(error, 'danger');
													},function(){
														 self.notify('Il file non esiste', 'warning');
													});
							});
						});


						
				}
			}




		}
	}
</script>




